import { useState, useEffect } from 'react';
import axios from 'axios';
import { parseStringPromise as parseXML } from 'xml2js';
import {
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableFooter
} from '@material-ui/core'

import './App.css';
import settings from './settings/index'

const tableHead = settings.tableHead.filter(col => col.display)

const cleanCatalog = (catalog) => {
  const timestamp = catalog.TIMESTAMP[0];
  const rates = catalog.RATE.map(rate => ({
    isFlagged: rate.ISFLAGGED[0] === '**' ? true : false,
    flagURL: rate.FLAGURL[0],
    flag: <img src={rate.FLAGURL[0]} alt={rate.FLAGURL[0]} className="flag" />,
    iso: rate.ISO[0],
    name: rate.NAME[0],
    country: rate.COUNTRY[0],
    id: rate.ID ? rate.ID[0] : '',
    weBuy: rate.WEBUY[0],
    weSell: rate.WESELL[0],
    invBuy: rate.INVBUY[0],
    invSell: rate.INVSELL[0]
  }));

  return { timestamp, rates }
}

const getCatalog = async (setCatalog) => {
  const res = await axios.get('rateswithcss.xml', {
    headers: { 'Cache-Control': 'no-cache' }
  });
  const xml = await parseXML(res.data);
  setCatalog(cleanCatalog(xml.CATALOG));
};

function App() {
  const [catalog, setCatalog] = useState({});

  useEffect(() => {
    getCatalog(setCatalog)
    const interval = setInterval(() => getCatalog(setCatalog), 5000)

    return () => clearInterval(interval)
  }, []);

  const timestamp = catalog.timestamp || '';
  const rates = catalog.rates || [];

  return (
    <div className="App">
      <div>
        <img
          className="logo"
          src="https://www.clearviewsys.com/images/Clear-View-Systems-CVS-Logo.png" 
          alt="logo" 
        />
      </div>

      <Paper className="paper" elevation={3}>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                {tableHead.map(col => <TableCell align={col.align}>{col.title}</TableCell>)}
              </TableRow>
            </TableHead>

            <TableBody>
              {rates.filter(rate => rate.isFlagged).map(rate => (
                <TableRow id={rate.iso}>
                  {tableHead.map(col => (
                    <TableCell align={col.align}>{rate[col.key]}</TableCell>
                  ))}
                </TableRow>
              ))}
            </TableBody>

            <TableFooter>
              <TableRow>
                <TableCell colSpan={tableHead.length}>{timestamp}</TableCell>
              </TableRow>
            </TableFooter>
          </Table>
        </TableContainer>
      </Paper>
    </div>
  );
}

export default App;
